import './App.scss';
import React from 'react';
// import { Static } from './models/models';
import { Home } from './views/views';
// import { Loader, Footer, LoadingAnimation } from './components/components';
// import {
//   SplashContainer,
//   AboutContainer,
//   TimelineContainer,
//   SkillsContainer,
//   ExperienceContainer,
//   ProjectsContainer,
//   PensContainer,
// } from './containers/containers';

/**
 * App component
 */
class App extends React.Component {
  constructor(props) {
    super(props);
    this.updateCSSVars = this.updateCSSVars.bind(this);
  }

  componentDidMount() {
    window.addEventListener('resize', this.updateCSSVars);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateCSSVars);
  }

  updateCSSVars() {
    // set the real vh (workaround for mobile devices)
    document.documentElement.style.setProperty('--vh', `${window.innerHeight * 0.01}px`);
  }

  /**
   * Renders app
   */
  render() {
    return (
      <Home />
    );
  }
}

export default App;
